import React, { useEffect, useRef, useState } from "react";

import { HeadFC, HeadProps, PageProps } from "gatsby";
import { graphql } from "gatsby";

import {
  IWorkflowPageContext,
  IWorkflowStepsQueryResult,
} from "../types/workflow.type";

import Banner from "../components/common/banner";
import ContentContainer from "../components/common/contentContainer";
import WorkflowStep from "../components/workflow/workflowStep";
import WhyUsSection from "../components/whyUs/whyUsSection";

const Workflow = ({
  data,
  pageContext: { markupData, lang },
}: PageProps<IWorkflowStepsQueryResult, IWorkflowPageContext>) => {
  const workflowSteps = data.allMarkdownRemark.nodes;

  const isInitial = useRef<boolean>(true);
  const [scroll, setScroll] = useState<boolean>(false);

  const scrollHandler = () => {
    setScroll((scroll) => !scroll);
  };

  useEffect(() => {
    if (!isInitial.current || !window) return;
    isInitial.current = false;

    window.addEventListener("scroll", scrollHandler);

    return () => window.removeEventListener("scroll", scrollHandler);
  }, []);

  return (
    <>
      <Banner
        heightClass="h-136"
        title={markupData.bannerTitle}
        text={markupData.bannerText}
      />
      <div className="bg-secondary-100">
        <ContentContainer className="flex-col items-start p-6 md:py-24 relative transition-all duration-1000">
          {workflowSteps
            ? workflowSteps.map((step, i) => (
                <WorkflowStep
                  key={step.id}
                  step={step}
                  className="md:even:translate-x-full md:odd:flex-row-reverse md:-ms-6 md:odd:ms-6"
                  scroll={scroll}
                  stepNumber={i + 1}
                />
              ))
            : null}
          <div className="absolute left-1/2 top-0 bottom-0 border-r-8 border-solid border main-100 -translate-x-1/2" />
        </ContentContainer>
        <WhyUsSection lang={lang} />
      </div>
    </>
  );
};

export default Workflow;

export const Head: HeadFC<IWorkflowStepsQueryResult, IWorkflowPageContext> = ({
  pageContext,
}: HeadProps<IWorkflowStepsQueryResult, IWorkflowPageContext>) => (
  <title>{pageContext.markupData.head}</title>
);

export const query = graphql`
  query ($lang: String) {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "workflow" }, language: { eq: $lang } }
      }
      sort: { frontmatter: { step: ASC } }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
          image {
            publicURL
          }
        }
      }
    }
  }
`;
