import React, { useEffect, useState, useRef } from "react";

import { IWorkflowStep } from "../../types/workflow.type";

import Bond from "../../assets/workflow/bond.svg";

const WorkflowStep = ({
  step,
  scroll,
  stepNumber,
  className,
}: {
  step: IWorkflowStep;
  scroll: boolean;
  stepNumber: number;
  className?: string;
}) => {
  const div = useRef<HTMLDivElement>(null);
  const [coverWidth, setCoverWidth] = useState<"w-[100%]" | "w-0">("w-[100%]");

  useEffect(() => {
    if (!div.current || !window) return;
    const divBottom = div.current.getBoundingClientRect().bottom;

    if (window.innerHeight > divBottom) setCoverWidth("w-0");
  }, [scroll]);

  const {
    html,
    frontmatter: {
      title,
      image: { publicURL: imageURL },
    },
  } = step;

  return (
    <div
      className={`workflow-step relative md:w-1/2 flex flex-row items-center h-min my-20 first:mt-36 md:-my-2 md:first:mt-0 md:last:mb-0 text-secondary-100 z-10 md:z-0 ${className}`}
      ref={div}
    >
      <div
        className={`hidden md:block cover absolute -right-6 -top-2 -bottom-2 z-10 bg-secondary-100 transition-all duration-1000 ${coverWidth}`}
      />
      <p className="step-number absolute rounded-full h-12 w-12 flex items-center justify-center bg-main-100 text-secondary-100 font-extrabold text-2xl -translate-x-1/2 -translate-y-[120%] md:translate-x-full z-30 top-0 md:top-auto left-1/2 md:left-auto md:translate-y-0">
        {stepNumber}
      </p>
      <Bond className="hidden md:block basis-1/6" />
      <div
        className={`md:basis-5/6 flex flex-row justify-around h-min rounded-2xl p-10 bg-main-100 text-secondary-100`}
      >
        <img
          src={imageURL}
          alt={`${title} logo`}
          className="basis-1/6 mr-7 invert"
        />
        <div className="flex flex-col pb-4 basis-4/6">
          <h4 className="font-extrabold text-2xl pb-6">{title}</h4>
          <div
            className={`text-xl`}
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </div>
      </div>
    </div>
  );
};

export default WorkflowStep;
